import * as React from 'react';
import { PageProps } from 'gatsby';

import { Input, Layout, Seo } from '../components';

type DataProps = {
	semilla?: number;
	cantidadNumerosR?: number;
	respuesta: Array<Respuesta>;
};

type Respuesta = {
	semilla1: number;
	semilla2: number;
	numeoR: string;
	resultado: number;
	semilla: string;
};

class ProductosMedios extends React.Component<PageProps, DataProps> {
	constructor(props) {
		super(props);
	}

	render(): React.ReactNode {
		return (
			<Layout>
				<Seo title="Productos medios" />

				<div>
					<h1>Calculadora de productos medios</h1>
				</div>
			</Layout>
		);
	}
}
